<script>
  import SongResult from "./SongResult.svelte";
  import { findAlbums, albumTracks } from "../musixmatch.js";
  import { createEventDispatcher } from "svelte";
  const dispatch = createEventDispatcher();

  export let artists, qid;
  let selected = -1,
    lastSelected = -1;
  let albums = [],
    selected_albums = [];

  function flag(country) {
    if (!country) return "ðŸ³ï¸";

    // https://medium.com/binary-passion/lets-turn-an-iso-country-code-into-a-unicode-emoji-shall-we-870c16e05aad
    return country
      .toUpperCase()
      .replace(/./g, char => String.fromCodePoint(char.charCodeAt(0) + 127397));
  }

  $: if (lastSelected != selected) {
    lastSelected = selected;
    albums = [];
    findAlbums({
      artist_id: selected,
      page_size: 10,
      s_release_date: "desc"
    }).then(function(more) {
      if (!more) return;
      albums = [
        ...albums,
        ...more.map(e => ({
          ...e.album,
          songs: []
        }))
      ];
    });
  }

  function albumChange(event) {
    if (event.target.checked) {
      albums.forEach(a => {
        if (a.album_id == parseInt(event.target.value)) {
          // found it
          if (a.songs.length > 0) return;
          albumTracks({
            album_id: a.album_id,
            page_size: 100,
            f_has_lyrics: true
          }).then(function(more) {
            if (!more) return;
            a.songs = [...a.songs, ...more.map(e => e.track)];
            albums = albums; // ugh

            more
              .map(e => e.track)
              .forEach(t => {
                dispatch("select", {
                  track: t.track_id,
                  query_index: a.album_id + "-" + t.track_id,
                  title: t.track_name,
                  artist: t.artist_name
                });
              });
          });
          return;
        }
      });
    }
  }
</script>

<style>
  .songs {
    margin-left: 1em;
  }
</style>

{#each artists as a}
  <div>
    <input
      type="radio"
      value={a.artist_id}
      name="q{qid}"
      id="{qid}-{a.artist_id}"
      bind:group={selected} />
    <a href="https://www.musixmatch.com/artist/{a.artist_id}" target="_blank">
      {a.artist_name}
    </a>
    {flag(a.artist_country)}
    {#if a.artist_alias_list.length > 0}
      <em>
        (also known as {a.artist_alias_list
          .map(e => e.artist_alias)
          .join(', ')})
      </em>
    {/if}
  </div>
{/each}
{#if selected > -1}
  <h4>Albums ({selected})</h4>
  {#each albums as album}
    <label for="a{album.album_id}">
      <input
        type="checkbox"
        id="a{album.album_id}"
        on:change={albumChange}
        bind:group={selected_albums}
        value={album.album_id} />
      {album.album_name}
    </label>
    <div class="songs">
      {#if album.songs.length > 0}
        {#each album.songs as song, i}
          <SongResult
            {i}
            {song}
            album={true}
            qid="{album.album_id}-{song.track_id}"
            on:select />
        {/each}
      {/if}
    </div>
  {/each}
{/if}
