<script>
  import { createEventDispatcher } from "svelte";
  const dispatch = createEventDispatcher();

  export let song, i, qid;
  export let album = false;

  let {
    track_id,
    track_name,
    artist_name,
    album_name,
    has_lyrics,
    track_share_url
  } = song;

  function changed(e) {
    if (e.target.checked) {
      dispatch("select", {
        track: track_id,
        query_index: qid,
        title: track_name,
        artist: artist_name
      });
    } else {
      dispatch("select", {
        track: -1,
        query_index: qid
      });
    }
  }
</script>

<style>
  .red {
    color: red;
  }

  .faded {
    opacity: 0.5;
  }
</style>

<label for="t{track_id}" class:faded={has_lyrics == 0}>
  <input
    type={album ? 'checkbox' : 'radio'}
    value={i}
    name="q{qid}"
    id="t{track_id}"
    disabled={has_lyrics == 0}
    checked={album}
    on:change={changed} />
  <strong>{track_name}</strong>
  by {artist_name}
  {#if has_lyrics == 0}
    <span class="red">No lyrics available</span>
  {:else}
    <a href={track_share_url} target="_blank">View lyrics</a>
  {/if}
</label>
