<script>
  import SongQuery from "./search/SongQuery.svelte";
  import ArtistQuery from "./search/ArtistQuery.svelte";
  import { searchSong, searchArtist } from "./musixmatch.js";

  let artistNames = "";

  let songNames = "";
  let queries = [];

  let selection = {};
  let count = 0;

  function searchSongs() {
    songNames
      .trim()
      .split("\n")
      .forEach(q => {
        searchSong({
          q_track_artist: q.replace(/ *\([^)]*\) */g, ""),
          page_size: 5,
          page: 1,
          s_track_rating: "desc"
        }).then(function(tracks) {
          queries = [
            ...queries,
            {
              q: q,
              type: "song",
              songs: tracks.map(e => e.track),
              selected: -1
            }
          ];
        });
      });
    songNames = "";
  }

  function searchArtists() {
    artistNames
      .trim()
      .split("\n")
      .forEach(q => {
        searchArtist({
          q_artist: q.replace(/ *\([^)]*\) */g, ""),
          page_size: 5,
          page: 1
        }).then(function(artists) {
          queries = [
            ...queries,
            {
              q: q,
              type: "artist",
              artists: artists.map(e => e.artist)
            }
          ];
        });
      });
    artistNames = "";
  }

  function handleSelection(event) {
    if (event.detail.track == -1) {
      delete selection[event.detail.query_index];
    } else {
      selection[event.detail.query_index] = {
        id: event.detail.track,
        title: event.detail.title,
        artist: event.detail.artist
      };
    }
    count = Object.keys(selection).length;
  }

  function exportTrackList() {
    let text = "id\ttitle\tartist\t\n";
    let trackIds = [];
    for (let key in selection) {
      if (selection.hasOwnProperty(key)) {
        const e = selection[key];
        text += e.id + "\t";
        text += e.title + "\t";
        text += e.artist + "\n";

        trackIds.push(e.id);
      }
    }
    window.prompt("Track IDs for script:", trackIds.join(","));
    window.open(
      "data:text/csv;charset=utf-8," + encodeURIComponent(text),
      "_blank"
    );
  }
</script>

<style>
  textarea {
    display: block;
    width: 400px;
    min-height: 100px;
    margin-top: 1em;
  }
</style>

<h1>Export IDs</h1>

<form on:submit|preventDefault={searchSongs}>
  <h2>Song Title</h2>
  You can enter more than one title on separate lines for bulk searches:
  <textarea bind:value={songNames} />
  <button type="submit">Suggest titles</button>
</form>

<form on:submit|preventDefault={searchArtists}>
  <h2>Artist</h2>
  You can enter more than one artist on separate lines for bulk searches:
  <textarea bind:value={artistNames} />
  <button type="submit">Suggest artists</button>
</form>

{#each queries as q, qid (q)}
  <div>
    <h3>{q.q}</h3>

    {#if q.type == 'song'}
      <SongQuery on:select={handleSelection} q={q.q} {qid} songs={q.songs} />
    {:else}
      <ArtistQuery on:select={handleSelection} artists={q.artists} {qid} />
    {/if}
  </div>
{/each}

{#if queries.length > 0}
  <button on:click={exportTrackList}>
    Export {count} selected {count == 1 ? 'song' : 'songs'}
  </button>
{/if}
